import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams,AlertController } from 'ionic-angular';

import { ApiServiceProvider } from '../../providers/api-service/api-service';
import { Storage } from '@ionic/storage';
/**
 * Generated class for the HealthAndWellnessListPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-health-and-wellness-list',
  templateUrl: 'health-and-wellness-list.html',
})
export class HealthAndWellnessListPage {
  deviceId:any;
  login_token:any;
  employeeId:any;
  healthandwellnessList: any;
  errorMessage:any;
  constructor( private storage:Storage,private apiServices:ApiServiceProvider,
    private alertCtrl: AlertController,public navCtrl: NavController, public navParams: NavParams) {
      this.storage.get('employeeId').then(data => {
        this.employeeId = data;
        console.log(' employeeId== ', this.employeeId);
      });
      this.storage.get('login_token').then((data) => {
        console.log('showOnBoard value is111==', data);
        this.login_token = data;
      })

  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad ');
    this.getWellnessListFun();
  }
  getWellnessListFun(){

    this.storage.get('employeeId').then(data => {
      this.employeeId = data;
      console.log(' employeeId== ', this.employeeId);
    });
    this.storage.get('login_token').then((data) => {
      console.log('showOnBoard value is111==', data);
      this.login_token = data;
    })
    this.storage.get('deviceId').then(data => {
      this.deviceId = data;
      let apiKey = {
        "client_id":this.apiServices.clientId,
        "employee_id":this.employeeId,
        "device":this.apiServices.device,
        "device_id":this.deviceId,
        "app_version":this.apiServices.appVersion,
      };
      this.apiServices.healthAndWellnessListApi(apiKey, this.login_token)
        .subscribe((res) => {
          console.log('healthandwellnessList res==', res);
          if(res.success==1){
              this.healthandwellnessList=res.data; 
              console.log('healthandwellnessList data==', this.healthandwellnessList);      
              this.errorMessage='';               
          }else{
            this.errorMessage=res.message;
            
          }


        })

      })



  }

  seeDetails(data){
    this.navCtrl.push('HealthAndWellnessDetailsPage',{'data':data});
  }

  seeBreathingDetails(data){
    this.navCtrl.push('MenuBeWellBreathingPage',{'data':data});
  }


}



